"use client";

import Link from "next/link";
import { useState } from "react";

export default function Navbar() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <header style={styles.header}>
      <div style={styles.container}>
        <div style={styles.brandArea}>
          <Link href="/" style={styles.brandLink}>
            <span style={styles.logo} aria-hidden>
              ✈️
            </span>
            <span style={styles.brandText}>Travel Journal</span>
          </Link>
        </div>

        <nav style={styles.navDesktop} aria-label="Primary">
          <Link href="/" style={styles.navLink}>
            Home
          </Link>
          <Link href="/trips" style={styles.navLink}>
            Trips
          </Link>
          <Link href="/create" style={styles.navLinkPrimary}>
            Create Trip
          </Link>
        </nav>

        <button
          type="button"
          aria-label="Toggle menu"
          aria-expanded={isMenuOpen}
          onClick={() => setIsMenuOpen((v) => !v)}
          style={styles.menuButton}
        >
          <span style={styles.menuIcon} />
          <span style={styles.menuIcon} />
          <span style={styles.menuIcon} />
        </button>
      </div>

      {isMenuOpen && (
        <nav style={styles.navMobile} aria-label="Mobile">
          <Link
            href="/"
            style={styles.navMobileLink}
            onClick={() => setIsMenuOpen(false)}
          >
            Home
          </Link>
          <Link
            href="/trips"
            style={styles.navMobileLink}
            onClick={() => setIsMenuOpen(false)}
          >
            Trips
          </Link>
          <Link
            href="/create"
            style={styles.navMobilePrimary}
            onClick={() => setIsMenuOpen(false)}
          >
            Create Trip
          </Link>
        </nav>
      )}
    </header>
  );
}

